import "core-js/modules/es.array.push.js";
export default {
  name: "bookItem",
  props: {
    index: {
      type: Number
    },
    source: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  methods: {
    // 点击书籍跳转到详情页
    handleClickItem(item) {
      this.$router.push({
        name: "bookDetail",
        query: {
          id: item.id
        }
      });
    }
  }
};